		color: {
			type: String,
			default: '#de8c17'
		},
		bgColor: {
			type: String,
			default: '#fffbe8'
		},
		switchTime: {
			type: String,
			default: 3
		},
		showSerial: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			number: 0,
			list: [],
			copyText: '',
			show: ''
		};
